
html {
  background: #f4f4f4;
}
.slider {
  font-size: 0.5rem;
  padding: 0;
  margin: 0;
}
.slider > li {
  margin: 0.25rem 0;
  height: 1.7rem;
  line-height: 1.7rem;
  font-size: 0.35rem;
  width: 100%;
  position: relative;
  overflow: hidden;
}
.slider > li div {
  overflow: hidden;
  height: 1.7rem;
  line-height: 1.7rem;
}
.slider-left {
  transition: .25s ease;
  width: 100%;
  background: #fff;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 2;
  overflow: hidden;
  text-indent: .25rem;
}
.slider-right {
  position: absolute;
  right: 0;
  top: 0;
  z-index: 1;
  width: 4rem;
  text-align: center;
  overflow: hidden;
}
.mark {
  background: #ccc;
  width: 60%;
  float: left;
}
.delete {
  background: red;
  width: 40%;
  float: right;
}



