
.picker {
  height: 5rem;
  width: 100%;
  overflow: hidden;
  position: relative;
  box-flex: 1;
  -ms-flex: 1;
      flex: 1;
  -webkit-flex: 1;
}
.picker:before {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  height: 2rem;
  width: 100%;
  border-bottom: 1px #ccc solid;
  background-image: linear-gradient(to bottom, #FFF, rgba(255, 255, 255, 0));
  pointer-events: none;
  z-index: 1;
}
.picker:after {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  height: 2rem;
  width: 100%;
  background-image: linear-gradient(to top, #FFF, rgba(255, 255, 255, 0));
  border-top: 1px #ccc solid;
  pointer-events: none;
  z-index: 1;
}
.picker .picker-ul {
  text-align: center;
  transition: all 0.3s ease-out;
}
.picker .picker-ul > li {
  height: 1rem;
  line-height: 1rem;
  font-size: .4rem;
}



