
.alert {
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 9999;
  background: rgba(0, 0, 0, 0.5);
  position: fixed;
  transition: .25s;
}
.alert .alert-box {
  position: fixed;
  width: 82%;
  top: 50%;
  left: 50%;
  z-index: 9999;
  transform: translate3d(-50%, -50%, 0);
  background-color: #FAFAFC;
  text-align: center;
  border-radius: 6px;
  overflow: hidden;
}
.alert .alert-box .alert-tit {
  font-size: .5rem;
  line-height: 2;
}
.alert .alert-box .alert-content {
  padding: .25rem 0;
  font-size: .45rem;
}
.alert .alert-box .alert-content > input {
  -webkit-appearance: none;
  padding: 0;
  margin: 0;
  border-radius: 0;
  width: 80%;
  height: .75rem;
  line-height: .75rem;
  font-size: .45rem;
  border: 1px #ccc solid;
}
.alert .alert-box .alert-btn {
  display: box;
  display: -ms-flexbox;
  display: flex;
  display: -webkit-flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -webkit-justify-content: space-between;
}
.alert .alert-box .alert-btn > span {
  width: 50%;
  font-size: .4rem;
  color: #247aea;
  padding: .25rem 0;
  border-top: 1px #ccc solid;
}
.alert .alert-box .alert-btn > span:first-child {
  border-right: 1px #ccc solid;
}
.fade-enter-active,
.fade-leave-active {
  transition: 0.1s;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}



